import React from 'react';
import { connect } from 'react-redux';
import { getCards } from '../../../redux/cards/cards.actions';
import TabView from '../../../components/TabPanel/TabView';
import DetailsTab from './detailTab.jsx';
import HistoryTab from './historyTab';
import { getSelectedCard } from 'redux/cards/cards.selectors';
import TextSpinner from '../../../assets/SvgComponents/TextSpinner/TextSpinner';
import { t } from '../../../i18n';
import { CARDS_NAMESPACE } from '../constants';

class CardPanel extends React.Component {
  render() {
    let datas = {
      accountNumber: <TextSpinner />,
      ownerName: <TextSpinner />,
      activationFlag: <TextSpinner />,
      deliveryCardDate: <TextSpinner />,
      startValDate: <TextSpinner />,
      expiryDate: <TextSpinner />,
    };
    if (this.props.selectedCard) {
      datas = this.props.selectedCard;
    }

    return (
      <div className="col-sm-12 tab-carte">
        <div className="panel panel-default">
          <TabView>
            <div label={t(`${CARDS_NAMESPACE}DetailTab`)}>
              <DetailsTab
                data={{
                  accountNumber: datas.accountNumber,
                  accountName: datas.ownerName,
                  cardStatus: datas.activeState
                    ? t(`${CARDS_NAMESPACE}Active`)
                    : t(`${CARDS_NAMESPACE}NotActive`),
                  deliveryDate: datas.deliveryCardDate
                    ? datas.deliveryCardDate
                    : t(`${CARDS_NAMESPACE}Empty`),
                  validityDate: datas.startValDate,
                  expDate: datas.expiryDate,
                }}
              />
            </div>
            <div label="Plafonds">
              <h1>Oops! Sorry I'm not ready !! :( </h1>
            </div>
            <div label={t(`${CARDS_NAMESPACE}OperationHistory`)}>
              <HistoryTab />
            </div>
          </TabView>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getCards: () => {
    dispatch(getCards());
  },
});

const mapStateToProps = state => ({
  ClientCards: state.Cards.cards,
  selectedCard: getSelectedCard(state),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(CardPanel);
