import { connect } from 'react-redux';
import AccountCards from './components/feature';
import { getSelectedCard } from '../../redux/cards/cards.selectors';
import { activateTab } from '../../redux/cards/cards.actions';

const mapStateToProps = state => ({
  activeTab: state.Cards.activeTab,
  cards: state.Cards.cards,
  selectedCard: getSelectedCard(state),
  selectedCardNumber: state.Cards.selectedCardNumber,
});
const mapDispatchToProps = dispatch => ({
  activateTab: activeTab => {
    dispatch(activateTab(activeTab));
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AccountCards);
