import React from 'react';

class TabPanel extends React.Component {
  onClick = () => {
    const { label, onClick } = this.props;
    onClick(label);
  };

  render() {
    const {
      onClick,
      props: { activeTab, label },
    } = this;

    let className = '';

    if (activeTab === label) {
      className += 'active';
    }

    return (
      <li className={className} onClick={onClick}>
        <a href="#">
          <span className="icon">
            <img
              src={require(`../../assets/img/${label}-icon.svg`)}
              alt=""
              width="20"
            />
          </span>
          {label}
        </a>
      </li>
    );
  }
}
export default TabPanel;
