import React from 'react';
import { CardImgContainer } from './CardsElements.styles';
import { OpposDivContainer } from './CardsElements.styles';
import LockedCardSvg from '../../assets/SvgComponents/LockedCard/LockedCard';
const CardsElements = props => {
  let currentImage = null;
  try {
    currentImage = require(`../../assets/img/CardsImg/${props.CardImg}.png`);
  } catch (error) {
    currentImage = require(`../../assets/img/CardsImg/VISACLASSICBANE.png`);
  }

  return (
    <CardImgContainer
      key={props.key}
      CardActive={props.cardActive}
      onClick={props.onPickCard}
    >
      <OpposDivContainer CardOppos={props.CardOppos}>
        <LockedCardSvg />
      </OpposDivContainer>
      <img src={currentImage} alt="card" />
      <span className="numcompte"> {props.cardNumber}</span>
      <span className="nom">{props.ownerName}</span>
      <span className="date">Exp {props.expirationDate}</span>
    </CardImgContainer>
  );
};

export default CardsElements;
