import React from 'react';
import { connect } from 'react-redux';
import CardsCaroussel from '../../../components/CardCaroussel/CardCaroussel';
import CardActions from '../../../components/CardActions/CardActions';
import {
  getCards,
  selectCard,
  activate,
  oppose,
  deactivate,
} from '../../../redux/cards/cards.actions';
import {
  showMessage,
  hideMessage,
} from '../../../redux/messages/messages.actions';
import { getAccounts } from '../../../redux/accounts/accounts.actions';
import { getCardTransactions } from '../../../redux/cards/cards.actions';
import { getSelectedCard } from '../../../redux/cards/cards.selectors';
import { t } from '../../../i18n';
import { CARDS_NAMESPACE } from '../constants';

class CardsDetails extends React.Component {
  state = {
    active: 0,
    current: null,
    currentAccount: '',
  };
  componentDidMount() {
    this.props.getCards();
    this.props.getAccounts();
  }
  _cardChangeHandler = Current => {
    this.setState({ current: Current });

    if (this.props.ClientCards) {
      this.props.selectCard(this.props.ClientCards[Current].cardNumber);
      this.props.getCardTransactions(
        this.props.ClientCards[Current].cardNumber,
      );
      this.setState({
        currentAccount: this.props.ClientCards[Current].accountNumber,
      });
    }
  };
  _opposeCard = () => {
    this.props.opposeCard(this.props.selectedCardNumber);
    this.props.hideMessage();
  };
  _switchCardState = e => {
    if (e.value) {
      this.props.activateCard(this.props.selectedCardNumber);
    } else {
      this.props.deactivateCard(this.props.selectedCardNumber);
    }
    this.props.hideMessage();
  };

  render() {
    let label = t(`${CARDS_NAMESPACE}Activate`);
    this.props.selectedCard && this.props.selectedCard.activeState
      ? (label = t(`${CARDS_NAMESPACE}Deactivate`))
      : (label = t(`${CARDS_NAMESPACE}Activate`));
    let messageConfirm = t(`${CARDS_NAMESPACE}DeactivateConfirm`);
    this.props.selectedCard && this.props.selectedCard.activeState
      ? (messageConfirm = t(`${CARDS_NAMESPACE}DeactivateConfirm`))
      : (messageConfirm = t(`${CARDS_NAMESPACE}ActivateConfirm`));
    let CardsEl = this.props.ClientCards;
    let Solde = 0;
    this.props.Account.map(account => {
      if (account.accountNumber === this.state.currentAccount) {
        Solde = account.availableBalance;
        return Solde;
      }
      return null;
    });
    let ActionTab = (
      <CardActions
        label={label}
        cardActivated={
          this.props.selectedCard && this.props.selectedCard.activeState
        }
        solde={Solde}
        onSwitchchange={e => {
          this.props.showMessage(
            t(
              `${CARDS_NAMESPACE}${messageConfirm} ${this.props.selectedCardNumber}`,
            ),
            'WARN',
            t(`${CARDS_NAMESPACE}confirm`),
            true,
            e => this._switchCardState(e),
          );
        }}
        onOpposClick={() => {
          this.props.showMessage(
            t(
              `${CARDS_NAMESPACE}${t(`${CARDS_NAMESPACE}opposConfirm`)} ${
                this.props.selectedCardNumber
              }`,
            ),
            'DANG',
            t(`${CARDS_NAMESPACE}confirm`),
            true,
            () => this._opposeCard(),
          );
        }}
      />
    );
    return (
      <div className="position-relative">
        {ActionTab}
        <CardsCaroussel
          CardsElmts={CardsEl}
          SelectedElement={this._cardChangeHandler}
        />
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getCards: () => {
    dispatch(getCards());
  },
  getAccounts: () => {
    dispatch(getAccounts());
  },
  selectCard: cardNumber => {
    dispatch(selectCard(cardNumber));
  },
  getCardTransactions: cardNumber => {
    dispatch(getCardTransactions(cardNumber));
  },
  opposeCard: cardNumber => {
    dispatch(oppose(cardNumber));
  },
  activateCard: cardNumber => {
    dispatch(activate(cardNumber));
  },
  deactivateCard: cardNumber => {
    dispatch(deactivate(cardNumber));
  },
  showMessage: (
    text,
    severity,
    validateTitle,
    hasCancelBtn,
    handleValidate,
  ) => {
    dispatch(
      showMessage(text, severity, validateTitle, hasCancelBtn, handleValidate),
    );
  },
  hideMessage: () => {
    dispatch(hideMessage());
  },
});

const mapStateToProps = state => ({
  ClientCards: state.Cards.cards,
  Account: state.Accounts.accounts,
  isLoading: state.Cards.isLoading,
  error: state.Cards.error,
  selectedCardNumber: state.Cards.selectedCardNumber,
  selectedCard: getSelectedCard(state),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(CardsDetails);
