import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PageHeader from '../../../components/PageHeader/PageHeader';
import PageHeaderNav from '../../../components/PageHeadNav/PageHeadNav';
import { t } from '../../../i18n';
import { CARDS_NAMESPACE, TABS } from '../constants';
import CardsDetails from './cardsDetails';
import CardPanel from './cardPanel';

class Feature extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: TABS[0],
      currentCard: null,
    };
  }
  _activateTab = tab => {
    this.props.activateTab(tab);
  };
  _renderActiveTab = () => {
    switch (this.props.activeTab) {
      case 'cardsDetails':
        return (
          <div className="row mx-auto">
            <div className="col-sm-12">
              <div className="panel-body pl-0 pr-0 carte__page">
                <CardsDetails />
                <CardPanel current={this.state.currentCard} />
              </div>
            </div>
          </div>
        );
      default:
        return <CardsDetails />;
    }
  };
  render() {
    let tabs = ['cardsDetails'];
    return (
      <div className="page-content">
        <div className="page-content_side">
          <PageHeader text={t(`${CARDS_NAMESPACE}${this.props.activeTab}`)}>
            <li key="1">
              <Link to="/dashboard">{t(`${CARDS_NAMESPACE}homePage`)}</Link>
            </li>
            <li key="2">
              <Link to="/cards">
                {t(`${CARDS_NAMESPACE}breadCrumbSecondLink`)}
              </Link>
            </li>
            <li className="active" key="3">
              {t(`${CARDS_NAMESPACE}${this.props.activeTab}`)}
            </li>
          </PageHeader>
          <div className="page-content_inner">
            <PageHeaderNav>
              {tabs.map(tab => (
                <li key={tab}>
                  <Link
                    className={tab === this.props.activeTab && 'active'}
                    title={tab}
                    to="#"
                    onClick={() => this._activateTab(tab)}
                  >
                    {t(`${CARDS_NAMESPACE}${tab}`)}
                  </Link>
                </li>
              ))}
            </PageHeaderNav>
            <div className="main-content container-fluid">
              {this._renderActiveTab()}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Feature;
