import React from 'react';
import Coverflow from '../Caroussel/Caroussel';
import CardsElement from '../CardsElements/CardsElements';

class CardCaroussel extends React.Component {
  state = {
    active: 0,
  };
  render() {
    let Items = this.props.CardsElmts;
    let Count = 0;
    let element = Items.map((Cardelement, index) => {
      Count++;
      return (
        <CardsElement
          key={Count}
          cardActive={Cardelement.activeState}
          cardOppos={Cardelement.oppositionState}
          onPickCard={e => {
            this.props.SelectedElement(index);
          }}
          CardImg={Cardelement.cardImg}
          cardNumber={Cardelement.cardNumber}
          ownerName={Cardelement.ownerName}
          expirationDate={Cardelement.formatedExpirationDate}
        />
      );
    });
    return (
      <Coverflow
        width="1000"
        height="450"
        displayQuantityOfSide={3}
        navigation={false}
        enableScroll={true}
        clickable={true}
        active={this.state.active}
        enableHeading={false}
        currentFigureScale="1.9"
        handleActive={current => this.props.SelectedElement(current)}
      >
        {element}
      </Coverflow>
    );
  }
}

export default CardCaroussel;
