import React from 'react';
import DataTable from '../../../components/DataTable/CustomDataTable';
import { t } from '../../../i18n';
import { connect } from 'react-redux';
import FormatDate from '../../../components/FormattedDate/FormattedDate';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import DateCalender from '../../../components/DateCalendar/DateCalendar';
import { dateFilter } from '../../../utils/DateUtils';
import { getCardTransactions } from '../../../redux/cards/cards.actions';
import { CARDS_NAMESPACE } from '../constants';

class historyTab extends React.Component {
  constructor() {
    super();
    let maxDate = new Date();
    this.state = {
      maxDate: maxDate,
    };
  }
  _dateChangeHandler = () => {
    this.forceUpdate();
  };
  componentDidMount() {
    this.props.getCardTransactions(this.props.selectedCard);
  }
  _buildDataTableRows = () =>
    this.props.cardsTransactions.map(transaction => {
      const {
        transactionDate,
        transactionType,
        merchantDescription,
        transactionAmount,
        billingAmount,
      } = transaction;
      return {
        date: transactionDate,
        description: transactionType,
        trader: merchantDescription,
        amount: transactionAmount,
        amountPrev: billingAmount,
      };
    });
  _dateTemplate = (rowData, column) => (
    <FormatDate
      dateTime={rowData.dateValue}
      format={FormatsConfiguration.DateTimeFormat.fullDate}
    />
  );
  _dateCalendarComponent = maxDate => (
    <DateCalender
      maxDate={maxDate}
      handleDateChange={e => {
        this.dt.filter(e.value, 'date', 'custom');
        this._dateChangeHandler();
      }}
    />
  );
  render() {
    let rows = this._buildDataTableRows();
    return (
      <DataTable
        value={rows}
        onRowReorder={e => (rows = e.value)}
        reference={el => (this.dt = el)}
        columnResizeMode="fit"
        cols={[
          {
            field: 'date',
            header: t(`${CARDS_NAMESPACE}Date`),
            body: this._dateTemplate,
            filter: true,
            filterElement: this._dateCalendarComponent(this.state.maxDate),
            filterFunction: dateFilter,
          },
          {
            field: 'description',
            header: t(`${CARDS_NAMESPACE}Description`),
          },
          {
            field: 'trader',
            header: t(`${CARDS_NAMESPACE}Merchant`),
          },
          {
            field: 'amount',
            header: t(`${CARDS_NAMESPACE}Amount`, {
              currency: t('Components:currency'),
            }),

            body: this._amountTemplate,
          },
          {
            field: 'amountPrev',
            header: t(`${CARDS_NAMESPACE}AmountPrev`, {
              currency: t('Components:currency'),
            }),

            body: this._amountTemplate,
          },
        ]}
      />
    );
  }
}
const mapDispatchToProps = dispatch => ({
  getCardTransactions: cardNumber => {
    dispatch(getCardTransactions(cardNumber));
  },
});

const mapStateToProps = state => ({
  cardsTransactions: state.Cards.cardshistory,
  selectedCard: state.Cards.selectedCardNumber,
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(historyTab);
