import { actionCreator } from '../../utils/generators';
import CardsActionTypes from './cards.types';

export const getCards = () => ({
  type: actionCreator('req', CardsActionTypes.GET_CARDS),
});
export const selectCard = selectedCard => ({
  type: actionCreator('req', CardsActionTypes.SELECT_CARD),
  payload: selectedCard,
});
export const getCardTransactions = () => ({
  type: actionCreator('req', CardsActionTypes.GET_HISTORY),
});
export const activateTab = activeTab => ({
  type: actionCreator('req', CardsActionTypes.ACTIVATE_TAB),
  payload: activeTab,
});

export const activate = cardNumber => ({
  type: actionCreator('req', CardsActionTypes.ACTIVATE),
  payload: cardNumber,
});
export const deactivate = cardNumber => ({
  type: actionCreator('req', CardsActionTypes.DEACTIVATE),
  payload: cardNumber,
});

export const oppose = cardNumber => ({
  type: actionCreator('req', CardsActionTypes.OPPOSE),
  payload: cardNumber,
});
