import React from 'react';
import { t } from '../../../i18n';
import { CARDS_NAMESPACE } from '../constants';

const DetailTab = props => (
  <div className="tab-pane cont active">
    <div className="col-left">
      <div className="row-type">
        <label htmlFor="r">{t(`${CARDS_NAMESPACE}AccountNumber`)}</label>
        <strong>{props.data.accountNumber}</strong>
      </div>
      <div className="row-type">
        <label htmlFor="e">{t(`${CARDS_NAMESPACE}Holder`)}</label>
        <strong>{props.data.accountName}</strong>
      </div>
      <div className="row-type">
        <label htmlFor="y">{t(`${CARDS_NAMESPACE}Status`)}</label>
        <strong>{props.data.cardStatus}</strong>
      </div>
    </div>
    <div className="col-right">
      <div className="row-type">
        <label htmlFor="t">{t(`${CARDS_NAMESPACE}DeliveryDate`)}</label>
        <strong>{props.data.deliveryDate}</strong>
      </div>
      <div className="row-type">
        <label htmlFor="z">{t(`${CARDS_NAMESPACE}StartValidity`)}</label>
        <strong>{props.data.validityDate}</strong>
      </div>
      <div className="row-type">
        <label htmlFor="a">{t(`${CARDS_NAMESPACE}EndValidity`)}</label>
        <strong>{props.data.expDate}</strong>
      </div>
    </div>
  </div>
);

export default DetailTab;
