import React from 'react';

function Icon() {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      style={{ background: 'none' }}
      width="20"
      height="20"
      className="lds-eclipse"
      preserveAspectRatio="xMidYMid"
      viewBox="0 0 100 100"
    >
      <path
        fill="#e67b00"
        d="M23.738 20.812a40 40 0 0054 59.026 40 42 47.546 01-54-59.026"
      >
        <animateTransform
          attributeName="transform"
          begin="0s"
          calcMode="linear"
          dur="0.5s"
          keyTimes="0;1"
          repeatCount="indefinite"
          type="rotate"
          values="0 50 51;360 50 51"
        />
      </path>
    </svg>
  );
}

export default Icon;
