import React from 'react';
import { t } from '../../i18n';
import CardOpposing from '../../assets/SvgComponents/AccountOppositionIcone/AccountOppositionIcone';
import InputElement from '../../components/Ui/Input/Input';

const CardActions = props => (
  <div>
    <div className="price-total">
      <div className="solde--dispo animateds">
        <label htmlFor="Solde">{t('Components:availableBalance')}</label>
        <span>{props.solde} XOF</span>
      </div>
    </div>
    <div className="activation animateds">
      <div className="form-group">
        <InputElement
          elementType="SwitchLed"
          label={props.label}
          checked={props.cardActivated}
          onchange={props.onSwitchchange}
        />
      </div>
    </div>
    <div className="op animateds">
      <button className="button-opposer" onClick={props.onOpposClick}>
        <div>
          <CardOpposing />
        </div>
        <label className="col-sm-12 control-label" htmlFor="oppose">
          {t('Components:oppos')}
        </label>
      </button>
    </div>
  </div>
);

export default CardActions;
